/* Tab Container */
.Tabs {
    width: 100%;
    height: auto;
   
    background: #6A26DA;
    margin: 3.5rem auto 1.5rem;
    padding: 3rem 1rem;
    color: #E8F0F2;
    
  
  }
  
  /* Tab Navigation */
  ul.nav {
    width: 60%;
    margin: 0 auto 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #f0eee9;
    
  }
  ul.nav li {
    width: 25%;
    padding: 1rem;
    list-style: none;
    text-align: center;
    cursor: pointer;
    transition: all 0.7s;
    
  }
  ul.nav li:nth-child(2) {
    border-radius: 0;
    
  }
  ul.nav li:hover {
    background: rgba(50, 224, 196, 0.15);
  }
  ul.nav li.active {
    /* background: #39A2DB; */
    border-bottom: 1px solid #FFD166;
    color: #FFD166;
  }
  
  /* First and Second Tab Styles */
  .FirstTab p,
  .SecondTab p {
    font-size: 2rem;
    text-align: center;
  }

.project-m {
    width: 80%;
    height: auto;
    /* background: #fff; */
    margin: auto;
    margin-bottom: 3rem;
    margin-top: 6rem;
}
@media only screen and (max-width: 900px){
    .project-m {
        width: 100%;
    }  
    ul.nav{
        width: 100%;
    }
    .Tabs{
        pad: 2rem 0;
    }
}
